import { supabase } from './supabase';

export interface User {
  id: string
  name: string
  email: string
  created_at: string
}

export const userService = {
  // –†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è —Å –ø–∞—Ä–æ–ª–µ–º
  async register(name: string, email: string, password: string): Promise<User | null> {
    try {
      // 1. –†–µ–≥–∏—Å—Ç—Ä–∏—Ä—É–µ–º –≤ Supabase Auth
      const { data: authData, error: authError } = await supabase.auth.signUp({
        email: email.trim(),
        password: password.trim(),
        options: {
          data: {
            name: name.trim()
          }
        }
      });

      if (authError) {
        console.error('Auth error:', authError);
        alert(authError.message || '–û—à–∏–±–∫–∞ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏');
        return null;
      }

      if (!authData.user) {
        alert('–ù–µ —É–¥–∞–ª–æ—Å—å —Å–æ–∑–¥–∞—Ç—å –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è');
        return null;
      }

      // 2. –°–æ—Ö—Ä–∞–Ω—è–µ–º –≤ —Ç–∞–±–ª–∏—Ü—É users
      const { data: userData, error: userError } = await supabase
        .from('users')
        .insert([
          {
            id: authData.user.id,
            name: name.trim(),
            email: email.trim()
          }
        ])
        .select()
        .single();

      if (userError) {
        console.error('User table error:', userError);
        // –ï—Å–ª–∏ –æ—à–∏–±–∫–∞ –≤ —Ç–∞–±–ª–∏—Ü–µ, —É–¥–∞–ª—è–µ–º –∏–∑ auth
        await supabase.auth.admin.deleteUser(authData.user.id);
        return null;
      }

      return userData as User;
    } catch (err) {
      console.error('Unexpected error:', err);
      return null;
    }
  },

  // –í—Ö–æ–¥ —Å –ø–∞—Ä–æ–ª–µ–º
  async login(email: string, password: string): Promise<User | null> {
    try {
      // 1. –í—Ö–æ–¥ —á–µ—Ä–µ–∑ Supabase Auth
      const { data: authData, error: authError } = await supabase.auth.signInWithPassword({
        email: email.trim(),
        password: password.trim()
      });

      if (authError) {
        console.error('Login error:', authError);
        alert(authError.message || '–ù–µ–≤–µ—Ä–Ω—ã–π email –∏–ª–∏ –ø–∞—Ä–æ–ª—å');
        return null;
      }

      if (!authData.user) {
        alert('–ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –Ω–µ –Ω–∞–π–¥–µ–Ω');
        return null;
      }

      // 2. –ü–æ–ª—É—á–∞–µ–º –¥–∞–Ω–Ω—ã–µ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è –∏–∑ —Ç–∞–±–ª–∏—Ü—ã
      const { data: userData, error: userError } = await supabase
        .from('users')
        .select('*')
        .eq('id', authData.user.id)
        .single();

      if (userError) {
        console.error('Fetch user error:', userError);
        return null;
      }

      return userData as User;
    } catch (err) {
      console.error('Unexpected error:', err);
      return null;
    }
  },

  // –í—ã–π—Ç–∏
  async logout(): Promise<void> {
    await supabase.auth.signOut();
  },

  // –ü–æ–ª—É—á–∏—Ç—å —Ç–µ–∫—É—â–µ–≥–æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
  async getCurrentUser(): Promise<User | null> {
    try {
      const { data: { user } } = await supabase.auth.getUser();
      
      if (!user) return null;

      const { data: userData } = await supabase
        .from('users')
        .select('*')
        .eq('id', user.id)
        .single();

      return userData as User;
    } catch (err) {
      console.error('Error getting current user:', err);
      return null;
    }
  },

  // –ü–æ–ª—É—á–∏—Ç—å –≤—Å–µ—Ö –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–π
  async getUsers(): Promise<User[]> {
    try {
      const { data, error } = await supabase
        .from('users')
        .select('*')
        .order('created_at', { ascending: false });

      if (error) {
        console.error('Error fetching users:', error);
        return [];
      }

      return data as User[];
    } catch (err) {
      console.error('Unexpected error:', err);
      return [];
    }
  },

  // –£–¥–∞–ª–∏—Ç—å –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
  async deleteUser(id: string): Promise<boolean> {
    try {
      // –£–¥–∞–ª—è–µ–º –∏–∑ —Ç–∞–±–ª–∏—Ü—ã –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–π
      const { error: tableError } = await supabase
        .from('users')
        .delete()
        .eq('id', id);

      if (tableError) {
        console.error('Error deleting from users table:', tableError);
        return false;
      }

      // –£–¥–∞–ª—è–µ–º –∏–∑ auth (–Ω—É–∂–Ω—ã –ø—Ä–∞–≤–∞ –∞–¥–º–∏–Ω–∞)
      const { error: authError } = await supabase.auth.admin.deleteUser(id);
      if (authError) {
        console.error('Error deleting from auth:', authError);
      }

      return true;
    } catch (err) {
      console.error('Unexpected error:', err);
      return false;
    }
  },

  // –ü—Ä–æ–≤–µ—Ä–∏—Ç—å, –∞–≤—Ç–æ—Ä–∏–∑–æ–≤–∞–Ω –ª–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å
  async checkAuth(): Promise<boolean> {
    const { data: { session } } = await supabase.auth.getSession();
    return !!session;
  }
};