"use client";

import { useEffect, useState } from 'react';
import { userService, User } from '@/lib/user-service';
import Link from 'next/link';
import { useRouter } from 'next/navigation';

export default function DashboardPage() {
  const [user, setUser] = useState<User | null>(null);
  const [loading, setLoading] = useState(true);
  const router = useRouter();

  useEffect(() => {
    checkAuth();
  }, []);

  const checkAuth = async () => {
    const currentUser = await userService.getCurrentUser();
    if (!currentUser) {
      router.push('/');
      return;
    }
    setUser(currentUser);
    setLoading(false);
  };

  const handleLogout = async () => {
    await userService.logout();
    router.push('/');
  };

  if (loading) {
    return (
      <div style={{ padding: '40px', textAlign: 'center' }}>
        <p>–ó–∞–≥—Ä—É–∑–∫–∞...</p>
      </div>
    );
  }

  return (
    <div style={{ maxWidth: '800px', margin: '0 auto', padding: '20px' }}>
      <header style={{ 
        display: 'flex', 
        justifyContent: 'space-between',
        alignItems: 'center',
        marginBottom: '30px'
      }}>
        <h1>–õ–∏—á–Ω—ã–π –∫–∞–±–∏–Ω–µ—Ç</h1>
        <div style={{ display: 'flex', gap: '10px' }}>
          <Link href="/" style={{ padding: '8px 16px', background: '#08572f', color: 'white', textDecoration: 'none', borderRadius: '5px' }}>
            –ì–ª–∞–≤–Ω–∞—è
          </Link>
          <Link href="/blog" style={{ padding: '8px 16px', background: '#4a6fa5', color: 'white', textDecoration: 'none', borderRadius: '5px' }}>
            –ë–ª–æ–≥
          </Link>
          <button 
            onClick={handleLogout}
            style={{ padding: '8px 16px', background: '#dc3545', color: 'white', border: 'none', borderRadius: '5px', cursor: 'pointer' }}
          >
            –í—ã–π—Ç–∏
          </button>
        </div>
      </header>

      <main>
        <div style={{ 
          background: '#f8f9fa', 
          padding: '30px', 
          borderRadius: '10px',
          marginBottom: '30px'
        }}>
          <h2>üëã –ü—Ä–∏–≤–µ—Ç, {user?.name}!</h2>
          <p>–í–∞—à–∏ –¥–∞–Ω–Ω—ã–µ:</p>
          <ul>
            <li><strong>–ò–º—è:</strong> {user?.name}</li>
            <li><strong>Email:</strong> {user?.email}</li>
            <li><strong>–î–∞—Ç–∞ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏:</strong> {user?.created_at ? new Date(user.created_at).toLocaleDateString('ru-RU') : '–ù–µ–∏–∑–≤–µ—Å—Ç–Ω–æ'}</li>
            <li><strong>ID –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è:</strong> <small>{user?.id}</small></li>
          </ul>
        </div>

        <div style={{ 
          display: 'grid', 
          gridTemplateColumns: 'repeat(auto-fit, minmax(250px, 1fr))', 
          gap: '20px' 
        }}>
          <div style={{ background: '#e8f5e8', padding: '20px', borderRadius: '10px' }}>
            <h3>üìù –ë–ª–æ–≥</h3>
            <p>–ß–∏—Ç–∞–π—Ç–µ –∏ —Å–æ–∑–¥–∞–≤–∞–π—Ç–µ –ø–æ—Å—Ç—ã –≤ –Ω–∞—à–µ–º —Å–æ–æ–±—â–µ—Å—Ç–≤–µ</p>
            <Link href="/blog" style={{ display: 'inline-block', marginTop: '10px', color: '#08572f', fontWeight: 'bold' }}>
              –ü–µ—Ä–µ–π—Ç–∏ –≤ –±–ª–æ–≥ ‚Üí
            </Link>
          </div>

          <div style={{ background: '#e3f2fd', padding: '20px', borderRadius: '10px' }}>
            <h3>‚öôÔ∏è –ù–∞—Å—Ç—Ä–æ–π–∫–∏</h3>
            <p>–£–ø—Ä–∞–≤–ª–µ–Ω–∏–µ –≤–∞—à–∏–º –∞–∫–∫–∞—É–Ω—Ç–æ–º</p>
            <p style={{ fontSize: '14px', color: '#666' }}>(–í —Ä–∞–∑—Ä–∞–±–æ—Ç–∫–µ)</p>
          </div>

          <div style={{ background: '#fff3cd', padding: '20px', borderRadius: '10px' }}>
            <h3>üìä –°—Ç–∞—Ç–∏—Å—Ç–∏–∫–∞</h3>
            <p>–í–∞—à–∞ –∞–∫—Ç–∏–≤–Ω–æ—Å—Ç—å –≤ —Å–∏—Å—Ç–µ–º–µ</p>
            <p style={{ fontSize: '14px', color: '#666' }}>(–í —Ä–∞–∑—Ä–∞–±–æ—Ç–∫–µ)</p>
          </div>
        </div>
      </main>
    </div>
  );
}